.bg {
    background: linear-gradient(180deg, #405b75, hsla(0, 0%, 100%, 0));
}

.spec-container {
    max-height: 300px;
    /* 设置容器的最大高度，当内容过长时会出现滚动条 */
    overflow-y: scroll;
    /* 允许垂直滚动 */
    border: 4px solid #ccc;
    background-color: #303030;
    /* 添加一个边框，以便更好地显示容器边界 */
}

.spec {
    padding: 20px;
    gap: 20px;
    /* 添加一些内边距，以使内容与容器之间有间距 */
}

.warn p {
    text-shadow: 1px 1px #000;
    margin-top: 10px;
    margin-bottom: 10px;
}

.react-parallax {
    width: 100%;
}

.warn-btn {
    background-color: #305988;
    text-decoration: none;
    border-radius: 8px;
    width: 200px;
    height: 60px;
    display: grid;
    place-items: center;
    color: rgb(255, 255, 255);
    font-weight: 500;
    font-size: 24px;
    letter-spacing: 0.2rem;
}


.react-parallax>img {
    width: 100% !important;
}
body{
    margin: 0;
}