.digital {
  z-index: -1;
  position: relative;
  width: 100%;
}

.curve {
  max-width: 100%;
  object-fit: contain;
}

.abs-curve-text {
  min-height: 100vh;
  background-size: 100%;
  background-repeat: no-repeat;
  top: 50%;
  width: 100%;
  column-gap: 100px;
  padding-top: 4rem;
}

.digital-title {
  text-align: center;
  font-size: 20px;
  width: 80%;
}

.abs-curve-text>img {
  max-width: 100%;
  width: 100%;
  object-fit: contain;
}

.right-abs {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 2rem;
  color: rgb(0, 0, 0);
}

.right-abs h2 {
  font-weight: 500;
  font-size: 36px;
  text-shadow: 2px 2px 2px rgb(172, 172, 172);
}

.list {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 30px;
  padding: 1rem 3rem 1rem 3rem;
  flex-direction: row
}

.list p {

  gap: 40px;
  font-weight: 500;
  font-size: 16px;
}

.flex1 {
  flex: 1;
}

.box {
  width: 100%;
  height: 100%;
  margin: 5px;
  box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}

.box>img {
  width: 100%;
}

.contextImg {
  width: 90vw;
  max-width: 700px;
}

@media screen and (max-width: 1421px) {
  .list p {
    font-size: 25px;
  }

  .abs-curve-text>img {
    max-width: 700px;
  }

  .abs-curve-text {
    grid-template-columns: auto auto;
    gap: 40px;
  }

  .list {
    display: flex;
    grid-gap: 10px;
  }

}

@media screen and (max-width: 1285px) {
  .digital {
    width: 100%;
    transform: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(-30px);

  }

  .curve {
    display: none;
  }

  .abs-curve-text {
    grid-template-columns: 1fr;
    position: static;
    transform: none;
    padding-block: 50px;
    place-items: center;
    gap: 20px;
  }

  .right-abs {
    grid-row: 1/2;
    width: 100%;
    align-items: center;
  }

}

@media screen and (max-width: 500px) {
  .container {
    display: block;
  }

  .box {
    width: 100%;
  }

  .abs-curve-text {
    padding-top: 0rem;

  }

  .list {
    display: block;
  }

  .digital-title {
    text-align: center;
    font-size: 16px;
    width: 80%;
    padding-top: 2rem;
  }

  .right-abs h2 {
    font-size: 28px;
  }

  .right-abs {
    gap: 40px;
  }

  .list p {
    gap: 20px;
    font-size: 20px;
  }

  .list p img {
    object-fit: contain;
  }
}