.landing-hero {
  width: 100%;
  column-gap: 100px;
}

.index-hero-logo {
  width: 100%;
  height: 100vh;
  background-image: url('/public/assets/hero-1.png');
  background-repeat: no-repeat;
  background-size: 100%;
}


.landing-hero>img {
  /* align-self: flex-end; */
  max-width: 800px;
  width: 100%;
  object-fit: contain;
}

.left-hero {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}

.left-hero>img {
  width: 100%;
  height: 100vh;
}


.left-hero>.index-hero-sub-logo {
  width: 70%;
  max-width: 400px;
  margin-top: 2rem;
}

.left-hero h1 {
  color: #CCCCCC;
  font-weight: 500;
  font-size: 72px;
}

.left-hero div {
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: white;
  margin-block-end: 0em !important;
  margin-block-start: 0em !important;
}

.left-hero a {
  background-color: #305988;
  text-decoration: none;
  border-radius: 8px;
  width: 200px;
  height: 60px;
  display: grid;
  place-items: center;
  color: rgb(255, 255, 255);
  font-weight: 500;
  font-size: 24px;
  letter-spacing: 0.2rem;
}

@media screen and (max-width: 1285px) {
  .landing-hero {
    grid-template-columns: 1fr;
    place-items: center;
    row-gap: 50px;
  }

  .left-hero {
    align-items: center;
    text-align: center;
  }

  .index-hero-logo {
    background-image: url('/public/assets/hero-1-middle.png');
  }
}

@media screen and (max-width: 500px) {
  .left-hero h1 {
    font-size: 30px;
  }

  .left-hero {
    gap: 20px;
  }

  .left-hero div {
    font-size: 16px;
  }

  .index-hero-logo {
    max-height: 667px;
    background-image: url('/public/assets/hero-1-mobile.png');
  }
}