.products {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 40px;
  padding: 0 144px;
}

.products h3 {
  font-weight: 500;
  color: #e87a16;
  font-size: 32px;
}

.products h2 {
  font-weight: 500;
  font-size: 42px;
  color: #ffffff;
}

.product-row {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 40px;
  width: 100%;
}

.product-box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 40px;
  width: 100%;
  padding: 64px 24px 44px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.9);

  text-align: center;
}

.product-box img {
  width: 154px;
  object-fit: contain;
}

.product-box h1 {
  font-weight: 500;
  font-size: 32px;
  color: inherit;
}

.product-box p {
  font-weight: 500;
  font-size: 16px;
  color: var(--gray);
}

.product-box a {
  color: white;
  width: 100%;
  border-radius: 8px;
  height: 50px;
  display: flex;
  justify-content: center;
  font-weight: 500;
  font-size: 16px;
  align-items: center;
  text-decoration: none;
  margin-top: auto;
}


.product-box {
  color: black;
}

.text-left {
  text-align: left;
}

.productList {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(3, 3fr);
}

.productList-box {
  padding: 0px 4rem;

}

.red-dot {
  position: absolute;
  top: 0;
  right: 0;
  width: 10px;
  height: 10px;
  background-color: red;
  border-radius: 50%;
}

@media screen and (max-width: 1285px) {

  .products {
    transform: none;
    padding: 0 60px;
    width: 100%;
    border-radius: 0px;
  }

  .productList {
    grid-template-columns: repeat(1, 1fr);
  }

  .productList-box {
    padding: 0px 5rem;
    width: 100%;
  }

  .productList-font {
    font-size: 24px;
  }

  .productList-button {
    font-size: 24px !important;
    width: 40%;
  }
}

@media screen and (max-width: 990px) {
  .productList-font {
    font-size: 16px;
  }

  .productList-button {
    font-size: 14px !important;
    width: 70%;
  }

  .products {
    gap: 20px;
  }

  .products h2 {
    font-size: 30px;
    text-align: center;
  }

  .products h3 {
    font-size: 20px;
  }

  .product-row {
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (max-width: 500px) {
  .products {
    padding: 0 20px;
  }

  .products h2 {
    font-size: 22px;
  }
}